@charset "utf-8";
body{
  /* background-color: black;
  color:#fff; */
}
header img{
  width:80px;
  height:80px;
}

/* pc버전 */
@media screen and (min-width:768px){
  header{
    position:fixed;
    left:0px; top:0px;
    background-color: rgb(129, 77, 153);
    width:180px; height:100vh
    /* position: relative;
    width:100%; height:200px; */
  }
  header>h1{
    text-align:center;
    padding:15px 0px;
    /* position: absolute;
    width:100%; top:0px;
    text-align: center; */
  }
  main{
    position:absolute;
    width: calc(width-180px);
    left:180px; top:0px;
    padding:30px;
    box-sizing: border-box
    ;
  }
}
/*모바일버전*/
@media screen and (max-width:767px){
  header{
    position: absolute;
    width:100%; top:0px;
  }
  header h1{
    text-align: center;
  }
}

.load_img{
  width:80px; height:80px;
  position:fixed;
  top:50%; right:50%;
  transform: translate(-50%,-50%);
}
